.menu {
  display: flex;
  justify-content: space-between;

  &__logo {
    position: relative;
    margin-right: 35px;
    height: 30px;

    @include onDesktop {
      margin-right: 0;
    }
  }

  &__image {
    position: relative;
    top: 3px;
    width: 45px;
    height: 26px;

    @include onDesktop {
      top: 0;
    }
  }

  &__icon {
    align-self: flex-end;
    display: block;
    width: 24px;
    height: 24px;
    background-image: url("../images/menu/menu-button.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 18px 8px;

    &--cross {
      background-image: url("../images/menu/cross.svg");
      background-size: 24px;
    }

    @include onDesktop {
      display: none;
    }
  }
}
